const MainNote = ( {mainContent , setMainContent} )=>{

    console.log(mainContent[2])
    
    return (
    <div  className='overflow-hidden d-flex flex-column justify-content-center align-items-center'>
        <main className='w-75 d-flex flex-column justify-content-center'>
            <h3 > {mainContent[1]} </h3>
            <p className='w-75 overflow-hidden flex-wrap'> {mainContent[0]} </p>
            <ul className="list-group"> {typeof(mainContent[2]) === typeof([ ]) ? mainContent[2].map(activity=>(
            <li  className="overflow-auto list-group-item d-flex flex-row align-items justify-content-between"> {activity}
                <i className="mx-1 deleteActivityBtn fa fa-minus-square "/>
            </li>
            )) : mainContent[2] } </ul>


        </main>
        <button className='w-25 position-absolute bottom-0 mb-2 btn btn-danger' >
        Close Note
        </button>
    </div>
)}

export default MainNote