import React, { useState } from 'react';

const Note = ({ createdAt, activities, id, initialTitle, initialText, removeNote, updateNote, updatedAt, setMainContent, key}) => {

  // note title state
  const [title, setTitle] = useState(initialTitle);
  // note text state
  const [text, setText] = useState(initialText);
  // editable state
  const [editable, setEditable] = useState(false);

  // handlers
  // save handler
  const handleSave = () => {
      updateNote(id, title, text);
      setEditable(!editable);
  };


  // CSS override de bootstrap


let activitiesList = []
if (activities) {
  activities.forEach(activity =>{
    if (activity.title) {
      activitiesList.push(activity.title)
    }else{
      activitiesList.push(activity)
    }
  }) 
}
const handleCardClick = ()=>{
  console.log("lista:", activitiesList)
  setMainContent([text, title, activitiesList])
};
  console.log(new Date(createdAt).getDate())
  
  console.log(new Date(createdAt).getMonth())

  // render JSX
  return (
    <div className="note card  col-10" key={key}>
      <div className="card-body" onClick={handleCardClick} >
        <p className='p-1' style={{borderRadius:'5px',backgroundColor:'#333', color:'#fff', position:'absolute'}}>{new Date(createdAt).getDate() + "/" +  new Date(createdAt).getMonth() }</p>
        <h2
          className={"noteTitle"}
          spellCheck={false}
          disabled={!editable}
          value={title}
          onChange={(e) => setTitle(e.target.value)}
        >{title}</h2>
        <br />
        <button className="btn" onClick={() => {removeNote(id)}}>
          <i className="text-danger fa fa-trash fa-lg"></i>
        </button>
      </div>
    </div>
  );
};

export default Note;