// imports
import React, { useState, useEffect } from 'react';
import axios from 'axios';
//import Header from './Header';
import NotesList from './NotesList';
import NoteForm from './NoteForm';
//import Footer from './Footer';
import './App.css'
import MainNote from './MainNote';


const App = () => {
  // useState hook (las notas de la lista)
  const [notes, setNotes] = useState([]);
  const [sideBarOn, setSideBarOn] = useState([true])
  const [addNoteOn, setAddNoteOn] = useState([true])
  const [addActivity, setAddActivity] = useState(false)
  const [mainContent, setMainContent] = useState(["Title", "Text", ["Aa", "Bb"]]) 
  // useEffect hook (para fetchear la data al cargar)
  useEffect(() => {
    axios.get('/api/todolist')
      .then(res => {
        setNotes(res.data.toDoList);
      });
    
  }, []);

  // CRUD functions
  // crear nota
  const addNote = note => {
    axios.post('/api/todolist', note)
      .then(res => {
        const newNotes = [ res.data,...notes];
        setNotes(newNotes);
      });
  };

  // actualizar nota
  const updateNote = (id, title, text) => {
    const updatedNote = {
      title: title,
      text: text
    };
    axios.put('/api/todolist/' + id, updatedNote)
      .then(res => {
        const newNotes = notes.map(note =>
          note.id === id ? updatedNote : note
        );
        setNotes(newNotes);
      });
  };
    // delete activitie
    const updateActivities = (id, title, text, activities) => {
      const updatedNote = {
        title: title,
        text: text,
        activities: activities
      };
      axios.put('/api/todolist/' + id, updatedNote)
        .then(res => {
          const newNotes = notes.map(note =>
            note.id === id ? updatedNote : note
          );
          setNotes(newNotes);
        });
    };
  

  // eliminar nota
  const removeNote = (id) => {
      axios.delete('/api/todolist/' + id)
      .then(res => {
        const newNotes = notes.filter(note => note._id !== id);
        setNotes(newNotes);
    });
  };


  const handleClick = ()=>{
    console.log(document.querySelector('.noteListContainer').classList)
    setSideBarOn(prevState =>(!prevState))
    if(!addNoteOn){
      handleAddNoteBtn()
    }

  }
  const handleAddNoteBtn = () =>{
    setAddNoteOn(prevState =>(!prevState))
    if(addActivity){
      setAddActivity(prevState =>(!prevState))
    }
    if(sideBarOn){
      handleClick()
    }
  }
  return (
    <div>
      
      <div className="d-flex overflow-hidden  flex-row">
          <div  className="noteListContainer overflow-auto " style={sideBarOn ? {transition:".5s", marginLeft:"-315px", textAlign:'center'} : {textAlign:'center',transition:".8s", marginLeft:"0"}} >
            <NotesList notes={notes} removeNote={removeNote} updateNote={updateNote} setMainContent={setMainContent} />
          </div>
          <div className= "p-2 noteFormContainer "style={addNoteOn ? {width:'0',minWidth:'0',transition:'.5s',opacity:'0',visibility:'hidden', marginLeft:'0'} : {width:'300px',minWidth:'300px', transition:'.5s'}}>
            <NoteForm addActivity={addActivity} setAddActivity={setAddActivity} addNote={addNote} />
          </div>
          <div>
            <button onClick={handleAddNoteBtn} type="button" className="addNoteBtn mt-1" style={addNoteOn ? {} : {color:"red"}}  >
            <i  className="fa fa-plus-square" style={addNoteOn ? {transition:'.9s'} : {transition:'.9s', transform:"rotate(45deg)"}}></i>
            </button>
            <button type="button" className="sideBarBtn " onClick={handleClick} style={{transition:"2s"}}>
            <i style={sideBarOn ? {transition:".8s"}: {transition:".8s", transform:"rotate(180deg)"}} className="fa fa-chevron-right"></i>
            </button>
          </div>
          <div className="d-flex overflow-hidden flex-column justify-content-center w-100">
            <MainNote  mainContent={mainContent} setMainContent={setMainContent} />
          </div>
      </div>
    </div>
  );
};

export default App;